<template>
  <v-container>
    <v-layout row wrap class="mt-5 mb-2" v-if="loading">
      <v-flex xs12 class="text-xs-center">
        <v-progress-circular indeterminate color="teal" :width="7" :size="70"></v-progress-circular>
      </v-flex>
    </v-layout>
    <v-layout row wrap v-else>
      <v-flex xs12>
        <v-card>
          <v-card-title>
            <h3 class="teal--text">{{ meetup.title }}</h3>
            <template v-if="userIsCreator">
                <v-spacer></v-spacer>
                <app-edit-meetup-details :meetup="meetup"></app-edit-meetup-details>
</template>
          </v-card-title>
          <v-card-media :src="meetup.imageUrl"
           height="400px">
           </v-card-media>
<v-card-text>
  <div class="teal--text">{{ meetup.date | moment("LLLL") }} - {{ meetup.location }}</div>
<div><app-edit-meetup-date :meetup="meetup" v-if="userIsCreator"></app-edit-meetup-date><app-edit-meetup-time :meetup="meetup" v-if="userIsCreator"></app-edit-meetup-time></div>
<div class="grey--text mt-2">{{ meetup.description}}</div>
</v-card-text>
<v-card-actions>
  <v-spacer></v-spacer>
  <app-register-meetup :meetupId="meetup.id" v-if="userIsAuth"></app-register-meetup>
</v-card-actions>
        </v-card>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
  export default {
    props: ['id'],
    computed: {
      meetup() {
        return this.$store.getters.loadedMeetup(this.id);
      },
      userIsAuth() {
        return this.$store.getters.user !== null && this.$store.getters.user !== undefined
      },
      userIsCreator() {
        if (!this.userIsAuth) {
          return false
        }
        return this.$store.getters.user.id === this.meetup.creatorId
      },
      loading() {
        return this.$store.getters.loading
      }
    }
  };
</script>



