import Vue from 'vue'
import App from './App'
import * as firebase from 'firebase'
import router from './router'
import Vuetify from 'vuetify'
import 'vuetify/dist/vuetify.min.css'
import { store } from './store'
import dateFilter from './filters/date'
import AlertCmp from './components/shared/Alert.vue'
import EditMeetupDetails from './components/Meetup/Edit/EditMeetupDetails.vue'
import EditDate from './components/Meetup/Edit/EditDate.vue'
import EditTime from './components/Meetup/Edit/EditTime.vue'
import RegisterMeetup from './components/Meetup/Registration/RegisterMeetup.vue'
import moment from 'vue-moment'
import momentZt from 'moment-timezone'

Vue.use(Vuetify)
Vue.use(moment, { momentZt, })
Vue.config.productionTip = false
Vue.filter('date', dateFilter)
Vue.component('app-alert', AlertCmp)
Vue.component('app-edit-meetup-details', EditMeetupDetails)
Vue.component('app-edit-meetup-date', EditDate)
Vue.component('app-edit-meetup-time', EditTime)
Vue.component('app-register-meetup', RegisterMeetup)
    /* eslint-disable no-new */
new Vue({
    el: '#app',
    router,
    store,
    render: h => h(App),
    created() {
        firebase.initializeApp({
            apiKey: 'AIzaSyBQcBriunISasPMjWI_SC_dc7c61FOgXo8',
            authDomain: 'meetupdev-d2eca.firebaseapp.com',
            databaseURL: 'https://meetupdev-d2eca.firebaseio.com',
            projectId: 'meetupdev-d2eca',
            storageBucket: 'gs://meetupdev-d2eca.appspot.com'
        })
        firebase.auth().onAuthStateChanged((user) => {
            if (user) {
                this.$store.dispatch('autoSignIn', user)
                this.$store.dispatch('fetchUserData')
            }
        })
        this.$store.dispatch('loadMeetups')
    }
})


