<template>
  <v-container>
    <v-layout row wrap>
      <v-flex xs12 sm6 class="text-xs-center text-sm-right">
        <v-btn large router to="/meetups" dark class="teal darken-3"> Explore Meetups</v-btn>
      </v-flex>
      <v-flex xs12 sm6 class="text-xs-center text-sm-left">
        <v-btn large router to="/meetup/new" dark class="teal darken-3"> Organaze Meetup</v-btn>
      </v-flex>
    </v-layout>
    <v-layout class="mt-3 mb-2">
      <v-flex xs12 class="text-xs-center">
         <v-progress-circular
      indeterminate
      color="teal"
      :width="7"
      :size="70"
      v-if="loading"
    ></v-progress-circular>
      </v-flex>
    </v-layout>
    <v-layout row wrap v-if="!loading">
      <v-flex xs12>
        <v-carousel style="cursor: pointer;">
          <v-carousel-item v-for="meetup in meetups" :key="meetup.id" :src="meetup.imageUrl" @click="onLoadMeetup(meetup.id)" reverse-transition="fade" transition="fade">
              <div class="title text-sm-center"> {{ meetup.title }}</div>
          </v-carousel-item>
        </v-carousel>
      </v-flex>
    </v-layout>
    <v-layout row wrap class="mt-2 text-sm-center">
      <v-flex xs12>
        <p> Joint us and sheer your best city images ! </p>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
  export default {
    computed: {
      meetups () {
        return this.$store.getters.featuredMeetups
      },
      loading () {
        return this.$store.getters.loading
      }
    },
    methods: {
onLoadMeetup (id) {
this.$router.push('/meetup/'+ id)
}
    }
  };
</script>

<style scoped>
  .title {
    position: absolute;
    left: 40%;
    bottom: 50px;
    background-color: rgba(0, 0, 0, 0.3);
    color: rgb(160, 153, 153);
    font-size: 1.5em;
    padding: 10px;
    font-family: Poppins;
    min-width: 180px;
  }
</style>



