<template>
  <v-container>
    <v-layout row wrap class="mt-5 mb-2" v-if="loading">
      <v-flex xs12 class="text-xs-center">
        <v-progress-circular indeterminate color="teal" :width="7" :size="70"></v-progress-circular>
      </v-flex>
    </v-layout>
    <v-layout row wrap v-for="meetup in meetups" :key="meetup.id" class="mb-2" v-else>
      <v-flex xs12 sm10 md8 offset-sm1 offset-md2>
        <v-card class="teal darken-1" >
          <v-container fluid>
            <v-layout row>
              <v-flex xs5 sm4 md4>
                <v-card-media :src="meetup.imageUrl" height="125px"></v-card-media>
              </v-flex>
              <v-flex xs-6 sm7 md7>
                <v-card-title primary-title>
                  <div>
                    <h5 class="white--text">{{ meetup.title }}</h5>
                    <div> {{ meetup.date | moment('LLLL')}} </div>
                  </div>
                </v-card-title>
                <v-card-actions>
                  <v-btn flat :to="'/meetup/' + meetup.id">
                    <v-icon left>arrow_forward</v-icon>
                    View Meetup</v-btn>
                </v-card-actions>
              </v-flex>
              <v-flex>
              <v-icon color="warning" v-if="isRegistered(meetup.id)"> star</v-icon>
              </v-flex>
            </v-layout>
          </v-container>
        </v-card>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
  export default {
    computed: {
      meetups () {
        return this.$store.getters.loadedMeetups
      },
      loading() {
        return this.$store.getters.loading
      }
    },
    methods: {
      isRegistered(meetupId) {
        return this.$store.getters.isRegistered(meetupId)
      }
    }
  }
</script>



