import * as firebase from 'firebase'

export default {
    state: {
        loadedMeetups: [{
                imageUrl: "https://upload.wikimedia.org/wikipedia/commons/4/4b/Vue-ville-oran.jpg",
                id: '1',
                title: "Vue ville Oran",
                location: 'Oran, Algeria',
                description: 'Lorem ipsum dolor sit amet consectetur adipisicing elit. Vitae incidunt libero sequi minus earum tempore accusantium nihil est architecto voluptate! Quidem sint at beatae a inventore rerum voluptates asperiores magnam!',
                date: new Date()
            },
            {
                imageUrl: "https://s0.geograph.org.uk/geophotos/04/57/18/4571854_b4549146.jpg",
                id: '2',
                title: "Oran Parish Church",
                location: 'Oran, Algeria',
                description: 'Lorem ipsum dolor sit amet consectetur adipisicing elit. Vitae incidunt libero sequi minus earum tempore accusantium nihil est architecto voluptate! Quidem sint at beatae a inventore rerum voluptates asperiores magnam!',
                date: new Date()
            },
            {
                imageUrl: "https://upload.wikimedia.org/wikipedia/commons/b/b6/-front-de-mer-d-oran.jpg",
                id: '3',
                title: "front de mer d oran",
                location: 'Oran, Algeria',
                description: 'Lorem ipsum dolor sit amet consectetur adipisicing elit. Vitae incidunt libero sequi minus earum tempore accusantium nihil est architecto voluptate! Quidem sint at beatae a inventore rerum voluptates asperiores magnam!',
                date: new Date()
            }
        ],
    },
    mutations: {

        setLoadedMeetups(state, payload) {
            state.loadedMeetups = payload
        },
        createMeetup(state, payload) {
            state.loadedMeetups.push(payload)
        },
        updateMeetup(state, payload) {
            const meetup = state.loadedMeetups.find(meetup => {
                return meetup.id === payload.id
            })
            if (payload.title) {
                meetup.title = payload.title
            }
            if (payload.description) {
                meetup.description = payload.description
            }
            if (payload.date) {
                meetup.date = payload.date
            }
        }
    },
    actions: {

        loadMeetups({ commit }) {
            commit('setLoading', true)
            firebase.database().ref('meetups').once('value')
                .then((data) => {
                    const meetups = []
                    const obj = data.val()
                    for (let key in obj) {
                        meetups.push({
                            id: key,
                            title: obj[key].title,
                            description: obj[key].description,
                            imageUrl: obj[key].imageUrl,
                            location: obj[key].location,
                            date: obj[key].date,
                            creatorId: obj[key].creatorId
                        })
                    }
                    commit('setLoadedMeetups', meetups)
                    commit('setLoading', false)
                })
                .catch(
                    (error) => {
                        console.log(error)
                        commit('setLoading', false)
                    }
                )
        },
        createMeetup({ commit, getters }, payload) {
            const meetup = {
                title: payload.title,
                location: payload.location,
                description: payload.description,
                date: payload.date.toISOString(),
                creatorId: getters.user.id
            }
            let imageUrl
            let key
            firebase.database().ref('meetups').push(meetup)
                .then((data) => {
                    key = data.key
                    return key
                })
                .then(key => {
                    const filename = payload.image.name
                    const ext = filename.slice(filename.lastIndexOf('.'))
                    return firebase.storage().ref('meetups/' + key + '.' + ext).put(payload.image)
                })
                .then(fileData => {
                    firebase.storage().ref(fileData.metadata.fullPath).getDownloadURL()
                        .then((url) => {
                            imageUrl = url
                            return firebase.database().ref('meetups').child(key).update({ imageUrl: url })
                        })
                        // return firebase.database().ref('meetups').child(key).update({ imageUrl: imageUrl })
                })
                .then(() => {
                    commit('createMeetup', {
                        ...meetup,
                        imageUrl: imageUrl,
                        id: key
                    })
                })
                .catch((error) => {
                    console.log(error)
                })
                // Reach out to firebase and store it
        },
        updateMeetupData({ commit }, payload) {
            commit('setLoading', true)
            const updateObj = {}
            if (payload.title) {
                updateObj.title = payload.title
            }
            if (payload.description) {
                updateObj.description = payload.description
            }
            if (payload.date) {
                updateObj.date = payload.date
            }
            firebase.database().ref('meetups').child(payload.id).update(updateObj)
                .then(() => {
                    commit('setLoading', false)
                    commit('updateMeetup', payload)
                })
                .catch(error => {
                    console.log(error)
                    commit('setLoading', false)
                })
        }
    },
    getters: {
        loadedMeetups(state) {
            return state.loadedMeetups.sort((meetupA, meetupB) => {
                return meetupA.date > meetupB.date
            })
        },
        featuredMeetups(state, getters) {
            return getters.loadedMeetups.slice(0, 5)
        },
        loadedMeetup(state) {
            return (meetupId) => {
                return state.loadedMeetups.find((meetup) => {
                    return meetup.id === meetupId
                })
            }
        }
    }
}


